import {
  KameleoonCore,
  SDKConfigurationType,
  SDKType,
} from '@kameleoon/javascript-sdk-core';
import { StorageConstructor } from './storage';
import { RequestDispatcher } from './requester';
import { EventSourceConstructor } from './eventSource';

/**
 * @class
 * KameleoonClient - a class for creating kameleoon client instance
 * */
export class KameleoonClient extends KameleoonCore {
  constructor(siteCode: string, configuration?: Partial<SDKConfigurationType>) {
    const storage = new StorageConstructor();
    const eventSource = new EventSourceConstructor();
    const requestDispatcher = new RequestDispatcher();

    super({
      siteCode,
      configuration,
      internalConfiguration: {
        externalStorage: storage,
        externalEventSource: eventSource,
        externalRequestDispatcher: requestDispatcher,
        externalPackageInfo: {
          type: SDKType.JavaScript,
          version: process.env.npm_package_version as string,
        },
      },
    });
  }
}
